export default {
  name: "MkdirModal",
  data() {
    return {
      dirName: null
    };
  },
  props: {
    show: {
      type: Boolean,
      default: false
    },
    cancelHandler: Function
  },
  inject: ['mkdirHandler'],
  methods: {
    confirmHandler() {
      this.mkdirHandler(this.dirName);
    }
  }
};