export default {
  name: "FilePerformModal",
  props: {
    show: {
      type: Boolean,
      default: false
    },
    name: {
      type: String
    },
    cancelHandler: Function
  },
  inject: ['path'],
  methods: {
    downHandler() {
      window.open("api/file/get-file?path=" + encodeURI(this.path.join('') + this.name));
    }
  }
};