import { FolderOpenRound, InsertDriveFileRound, DeleteOutlineOutlined, ContentCopyRound, ContentCutRound, DriveFileRenameOutlineOutlined } from "@vicons/material";
import axios from "axios";
import ConfirmDeleteModal from "@/components/manages/filemanager/modals/ConfirmDeleteModal";
import RenameModal from '@/components/manages/filemanager/modals/RenameModal';
export default {
  name: "singleEntry",
  props: {
    type: Boolean,
    filename: String
  },
  components: {
    ConfirmDeleteModal,
    FolderOpenRound,
    InsertDriveFileRound,
    DeleteOutlineOutlined,
    ContentCopyRound,
    ContentCutRound,
    DriveFileRenameOutlineOutlined,
    RenameModal
  },
  data() {
    const setVis = () => {
      if (this.filename === '..') return;
      this.vis = true;
    };
    const disVis = () => {
      this.vis = false;
    };
    return {
      inCopyStatus: false,
      vis: false,
      setVis,
      disVis,
      confirm: false,
      renameShow: false,
      ownPath: this.path.join('') + this.filename
    };
  },
  setup() {
    return {
      FolderOpenRound
    };
  },
  provide() {
    return {
      renameConfirmHandler: this.renameConfirmHandler
    };
  },
  inject: ['reload', 'path', 'selectedCopyFile'],
  methods: {
    renameHandler(event) {
      event.stopPropagation();
      this.renameShow = true;
    },
    copyHandler(event, isCut) {
      event.stopPropagation();
      this.selectedCopyFile(this.ownPath, isCut);
    },
    cancelHandler() {
      this.confirm = false;
    },
    renameCancelHandler() {
      this.renameShow = false;
    },
    renameConfirmHandler(newName) {
      axios.post("api/file/rename", {
        from: this.ownPath,
        to: newName
      }).then(r => {
        if (r.data['success'] === true) this.reload();else this.renameShow = false;
      });
    },
    deleteConfirmHandler() {
      axios.post("api/file/delete", {
        filePath: this.ownPath
      }).then(r => {
        if (r.data['success'] === true) this.reload();else {
          this.confirm = false;
        }
      });
    },
    deleteHandler(event) {
      event.stopPropagation();
      this.confirm = true;
    }
  }
};